<template>
	<div class="card" style="width: 23rem">
		<div class="card-body" id="offcanvasExampleLabel">
			<h5 class="card-title">My Plekollections</h5>
			<div
				v-for="(item, index) in list_of_plekollections"
				class="plekjeslist"
			>
				<div
					@click="selectPlekollection(item, index)"
					v-bind:class="{ selected: index == selected_id }"
				>
					{{ item.name }}
				</div>
			</div>
		</div>
	</div>
	<div v-if="selected_plekollection.name">
		<div class="card" style="width: 23rem">
			<div class="card-body" id="offcanvasExampleLabel">
				<h5 class="card-title">
					{{ selected_plekollection.name }} &nbsp;
					<!-- 							data-bs-toggle="offcanvas"
							data-bs-target="#offcanvasExample"
							aria-controls="offcanvasExample" -->
					<button class="btn" @click="showPlekollectionOnMap()">
						Showtime
					</button>
				</h5>
				<div
					v-for="(item, index) in selected_plekollection.plekjes"
					class="plekjeslist"
				>
					<div
						@click="getPlekje(item.name)"
						v-bind:class="{ selected: item.selected }"
					>
						{{ item.name }}
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
var api = require("@/service");
import { useFeatureCollections } from "@/composables/useFeatureCollections";
export default {
	components: {
		useFeatureCollections,
	},
	setup() {
		const { featureCollections, addfeatureCollection } = useFeatureCollections();
		return {
			featureCollections,
			addfeatureCollection,
		};
	},
	data() {
		return {
			list_of_plekollections: [],
			selected_id: null,
			selected_plekollection: {},
            featureCollections:[]
		};
	},
	methods: {
		initPlekollectionList() {
			this.list_of_plekollections =
				this.$store.state.userData.my_plekollections;
		},
		selectPlekollection(item, i) {
			this.selected_id = i;
			this.getPlekollection(item._id);
		},
		getPlekollection(plekollection_id) {
			api.apiCall(
				"GET",
				process.env.VUE_APP_API_URL +
					"/plekollections/get_one/" +
					plekollection_id,
				null,
				(e) => {
					let mongodata = JSON.parse(e);
					this.selected_plekollection = mongodata;
				}
			);
		},
		showPlekollectionOnMap() {
			let list_of_ids = this.selected_plekollection.plekjes.map((x) => x.id)
			api.getListOfPlekjesFromListOfIds(list_of_ids,(res)=>{
                this.createFeatureCollectionAndSendToMap(res)
            });
		},
		createFeatureCollectionAndSendToMap(plekjes) {
			let geojsonFeatureCollection = {
				type: "FeatureCollection",
				name: this.selected_plekollection.name,
				features: [],
			};
			plekjes.forEach((e) => {
				geojsonFeatureCollection.features.push({
					type: "Feature",
					properties: {
						name: e.name,
					},
					geometry: e.geometry,
				});
			});
			this.addfeatureCollection(geojsonFeatureCollection)
		},
	},
	mounted() {
		this.initPlekollectionList();
	},
};
</script>

