import { createRouter, createWebHistory } from "vue-router";
import Store from "./store"

// these imports are done by lazy-loading
//import About from '../views/About.vue'
import Auth0callback from './views/Auth0callback.vue'

const routes = [
    {
		path: "/auth0callback",
		name: "auth0callback",
		component: Auth0callback,
	},
	{
		path: "/",
        redirect:"/map"
	},
    {
		path: "/map",
		name: "Map",
		// separate chunk lazy-loaded when the route is visited.
		component: () => import("./views/Map.vue"),
	},
	{
		path: "/about",
		name: "About",
		// separate chunk lazy-loaded when the route is visited.
		component: () => import("./views/About.vue"),
	},
	{
		path: "/profile",
		name: "Profile",
		// separate chunk lazy-loaded when the route is visited.
		component: () => import("./views/Profile.vue"),
		meta: { requiresAuth: true },
	},
];

const router = createRouter({
	history: createWebHistory(process.env.BASE_URL),
	routes,
});

router.beforeEach((to, from, next) => {

    if (to.matched.some((record) => record.path=="/auth0callback")){
        Store.dispatch('auth0HandleAuthentication');
        //next(false);
    }

    let routerAuthCheck = false;

    if(localStorage.getItem('access_token')&&localStorage.getItem('id_token')&&localStorage.getItem('expires_at')){
        let expiresAt = JSON.parse(localStorage.getItem('expires_at'));
        routerAuthCheck = new Date().getTime() < expiresAt;
    }

    // set global state 
    Store.commit('setUserIsAuthenticated',routerAuthCheck);

	if (to.matched.some((record) => record.meta.requiresAuth)) {
        if(routerAuthCheck){
            Store.commit('setUserIsAuthenticated',true);
            next();
        }else{
            router.replace('/');
        }
	}else{
        next();
    }
});

export default router;
